@import url(https://fonts.googleapis.com/css2?family=Antic&family=Montserrat:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

*{
    font-family: 'Antic', sans-serif !important;
    font-family: 'Montserrat', sans-serif;
    text-transform: uppercase;
    font-weight: 100 !important;
}
.background{
    background: url(/market-chart/static/media/1.5dbc808a.jpg);
    background-size: cover;
    height: 100%;
    top: 0px;
    left: 0px;
    right: 0px;
    bottom: 0px;
    position: absolute;
    z-index: -1;
}
h1{
    color: lightyellow;
    font-size: 72px;
    letter-spacing: 1;
}
button{
    border:none;
    margin: 5px;
    background-color: rgb(68, 68, 68);
    color: rgb(252, 252, 252);
    border-radius: 4px;
    cursor: pointer;
    letter-spacing: 1;
    transition: all 2.0s;
    -webkit-transition:all 2.0s;
    -moz-transition: all 2.0s;
    outline: none;
}
button:hover{
    background-color: rgb(75, 147, 182);
    color: ivory;
}
button:active{
    background-color: rgb(75, 147, 182);
    color: ivory;
}
button:focus {
    background-color: rgb(75, 147, 182);
    color: ivory;
}
.main{
    max-width: 1000px;
    margin: 100px auto;
    background-color: rgb(255, 255, 255);
    opacity: 0.9;
    border-radius: 6px;
}
.externalLinks{ 
    margin-bottom: 2px;
    left: 20px;
    position: absolute;
    bottom: 20px;
    font-size: 25px;
     cursor:pointer;
    margin: 0 auto;
     
}
#github{
    margin-left: 5px;
    color: rgb(255, 255, 255);
}
#linkedin{
    color: rgb(255, 255, 255);

}

@media (max-width: 860px) {
.externalLinks{
    bottom: 12px;

}

}

